.info {
    display:flex;
    margin-bottom: 30px;
    width: 97%;
    /* flex-wrap: wrap; */
    max-height: 300px;
}
@media only screen and (max-width: 614px){
    .info {
        display: flex;
        flex-wrap: wrap;
    }
    
    .info li {
        display: none;
    }
    .info img {
         Width: 100%!important;
         border-radius: 25px;
         display: flex;
         flex-wrap: wrap;
    }
    .info h3 {
      display: flex;
      color:grey;
      text-align: center;
      margin-top: 3px;

    }
}

.info img {
    width: 50%;
    height: 250px;
    border-top-left-radius: 25px;
}

.info li {
    color:turquoise;
    list-style: square ;
    margin-left: -15px;
    margin-top: 5px;
    
    
}

.info h3 {
    margin-left: 7px;
    
}